@import '../../global.scss';

.homeContainer {
    width: 100%;
    // height: 100vh;
    background-color: white;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    .getInTouch {
        position: fixed;
        left: -100px;
        top: 50%;
        transform: translateY(-50%);
        transform: rotate(-90deg);
        z-index: 1;
        animation: getInTouchFade 1s forwards;
        animation-delay: 4s;
        p {
            text-transform: uppercase;
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
        }
    }
    .heroContainer {
        padding: 0 $page-padding-x;
        height: 100vh;
        background-image: url('../../assets/Home/hero.jpg');
        background-position: 10% 30%;
        mask-image: url('../../assets/Home/intro.svg');
        mask-repeat: no-repeat;
        mask-position: 50%;
        animation: introZoom 2s forwards;
        animation-timing-function: cubic-bezier(1, 0, 0.6, 1.0);
        // animation-timing-function: ease-in-out;
        
        display: flex;
        justify-content: center;
        // align-items: center;
        flex-direction: column;
        h1 {
            font-style: normal;
            font-weight: 500;
            font-size: 56px;
            margin: 0;
            padding: 0;
            margin-bottom: 24px;
            position: relative;
            bottom: -150px;
            opacity: 0;

            animation: introHeadingFade 1s forwards, fadeIn 1s forwards;
            animation-delay: 3s, 3.25s;
        }
        .expandLine {
            width: 0;
            height: 1px;
            background-color: white;
            animation: introExpandLine 2s forwards;
            animation-delay: 1s;
        }
        h2 {
            font-style: normal;
            font-weight: normal;
            font-size: 24px;
            margin: 0;
            padding: 0;
            margin-top: 24px;
            position: relative;
            top: -85px;
            opacity: 0;

            animation: introSubheadingFade 1s forwards, fadeIn 1s forwards;
            animation-delay: 3s, 3.25s;
        }
    }
    .ourMission {
        height: 100vh;
        padding: 0 $page-padding-x;
        background-image: url('../../assets/Home/airport.jpg');
        background-repeat: no-repeat;
        background-size: cover;
        display: flex;
        flex-direction: column;
        justify-content: center;
        position: relative;
        align-items: center;
        .ourMissionText {
            width: 100%;
            h2 {
                font-style: normal;
                font-weight: 500;
                font-size: 64px;
                margin-bottom: 44px;
            }
            p {
                font-style: normal;
                font-weight: 500;
                font-size: 24px;
                line-height: 150%;
                width: 560px;
                letter-spacing: -0.015em;
            }
        }
        .floatingVideoContainer {
            position: absolute;
            // bottom: 0;
            bottom: calc(0% - 661px);

            .floatingVideo {
                border-radius: 32px;
                z-index: 1;
            }
        }
        
    }

    .airsideDriver {
        height: calc(100vh + 350px);
        padding: 0 $page-padding-x;
        padding-top: 350px;
        color: black;
        display: flex;
        flex-direction: column;
        justify-content: center;
        position: relative;
        .airsideDescription {
            z-index: 1;
            h2 {
                font-style: normal;
                font-weight: 500;
                font-size: 96px;
                line-height: 112px;
                letter-spacing: -0.015em;
                margin-bottom: 122px;
            }
            h3 {
                font-style: normal;
                font-weight: 500;
                font-size: 48px;
                line-height: 56px;
                letter-spacing: -0.015em;
                margin-bottom: 32px;
            }
            p {
                font-style: normal;
                font-weight: normal;
                font-size: 24px;
                line-height: 150%;
                letter-spacing: -0.015em;
                margin-bottom: 48px;
            }
            button {
                background: $aerovect-blue;
                color: white;
                width: 150px;
                height: 48px;
                border-radius: 8px;
                border: none;
                outline: none;
                font-style: normal;
                font-weight: normal;
                font-size: 16px;
                line-height: 150%;
                letter-spacing: -0.015em;
            }
        }

        .road {
            position: absolute;
            display: flex;
            align-items: flex-start;
            transform: scale(0.7);
            transform-origin: bottom right;
            bottom: 0;
            .roadLeft {
                position: relative;
                bottom: 0;
                .tractor {
                    position: absolute;
                    bottom: 0;
                    right: 0;
                }
            }
            .rightRoad {
                position: absolute;
                right: -130px;
            }
        }
    }
}