@import '../../global.scss';

.newsContainer {
    // display: flex;
    // flex-direction: column;
    header {
        // height: 
    }
    .news {
        padding: 200px $page-padding-x;
        // height: 100vh;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        header {
            padding: 0 $page-padding-x;
            position: absolute;
            top: 0;
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 100%;
            h3 {
                // flex-grow: 1;
                width: 100%;
                text-align: center;
                height: 75px;
                display: flex;
                justify-content: center;
                align-items: center;
                font-style: normal;
                font-weight: 700;
                font-size: 20px;
            }
            .split {
                height: 24px;
                width: 1px;
                background: #D2D2D2;
            }
            .underline {
                position: absolute;
                height: 4px;
                background-color: $aerovect-blue;
                width: 560px;
                bottom: 0;
                left: $page-padding-x;
                transition: all 0.25s;
            }
            .right {
                left: calc(100% - $page-padding-x - 560px);
            }
        }

        .coverageList {
            width: 100%;
            display: flex;
            flex-direction: column;
            .coverage {
                padding: 50px 0;
                width: 100%;
                display: flex;
                justify-content: space-between;
                border-bottom: 1px solid #D6D6D6;
                .article {
                    margin-left: 140px;
                    flex-grow: 1;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    h4 {
                        font-style: normal;
                        font-weight: 500;
                        font-size: 24px;
                    }
                    p {
                        font-style: normal;
                        font-weight: 400;
                        font-size: 16px;
                        color: #A0A0A0;
                    }
                }
                a {
                    margin-left: 250px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    text-decoration: none;
                    color: black;
                    p {
                        font-style: normal;
                        font-weight: 400;
                        font-size: 21px;
                        margin-right: 8px;
                    }
                    img {
                        width: 20px;
                        height: 20px;
                    }
                }
            }
        }

        .mediaKit {
            width: 100%;
            display: flex;
            flex-direction: column;
            .mediaRow:not(:last-child) {
                margin-bottom: 40px;
            }
            .mediaRow {
                display: flex;
                justify-content: space-between;
                flex-direction: row;
                .media {
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    position: relative;
                    p {
                        margin-top: 16px;
                        font-style: normal;
                        font-weight: 400;
                        font-size: 16px;
                    }
                    .hoveredImg {
                        border-top-right-radius: 8px;
                        border-bottom-right-radius: 8px;
                        position: absolute;
                        top: 0;
                        right: 0;
                        height: calc(100% - 16px - 16px);
                        width: 50%;
                        background: linear-gradient(270deg, #000000 0%, rgba(0, 0, 0, 0) 100%);
                        opacity: 0;
                        transition: all 0.25s;
                        pointer-events: none;
                        img {
                            position: absolute;
                            right: 40px;
                            top: 50%;
                            transform: translateY(-50%);
                        }
                    }
                }
                .media:hover .hoveredImg {
                    opacity: 1;
                    pointer-events: initial;
                }
            }
        }
    }

}