@import '../../../global.scss';

.imageCardContainer {
    width: 548px;
    height: 375px;
    position: relative;
    padding: 30px 36px;
    color: white;
    border-radius: 16px;
    img {
        height: 100%;
        width: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
    }
    header {
        font-style: normal;
        font-weight: 500;
        font-size: 32px;
        margin-bottom: 20px;
    }
    p {
        font-style: normal;
        font-weight: 500;
        font-size: 24px;
        line-height: 140%;
    }
}