@import '../../global.scss';

.technologyContainer {
    position: relative;
    isolation: isolate;
    // display: flex;
    // flex-direction: column;
    // justify-content: center;
    .intro, .background, .exploreMore {
        background-color: white;
        padding: 0 $page-padding-x;
        height: 100vh;
        width: 100vw;
        display: flex;
        align-items: center;
        position: fixed;
        h1 {
            font-style: normal;
            font-weight: 500;
            font-size: 88px;
            line-height: 140%;
            letter-spacing: -0.015em;
        }
    }
    .intro {
        z-index: 1;
    }
    .background {
        z-index: 2;
        background-image: url('../../assets/Technology/background.jpg');
        background-repeat: no-repeat;
        background-size: cover;
        background-position: 50%;
        color: white;
    }
    .exploreMore {
        justify-content: center;
        flex-direction: column;
        z-index: 3;
        color: black;
    }
    .background, .exploreMore {
        top: 0;
        left: 0;
        right: 0;
        mask-image: url('../../assets/Technology/circle.svg');
        mask-repeat: no-repeat;
        mask-position: 50%;
        mask-size: 0%;
    }

    .fullScreen {
        height: 100vh;
    }

    .statsContainer {
        width: 100%;
        height: 100vh;
        display: flex;
        justify-content: space-evenly;
        align-items: center;

        .sensorContainer {
            transform: translateY(-300px);
        }
        .lineContainer {
            display: flex;
            flex-direction: column;
            .line:not(:last-child) {
                margin-bottom: 68px;
            }
            .line {
                height: 400px;
                width: 1px;
                background: #9F9F9F;
            }
        }
        .dataContainer {
            .dataPoint {
                padding-left: 150px;
                position: relative;
                h3:not(:last-child) {
                    // margin-bottom: 35px;
                }
                h3 {
                    position: absolute;
                    left: 0%;
                    color: #E1E1E1;
                    font-style: normal;
                    font-weight: bold;
                    font-size: 120px;
                }
                p:not(:last-child) {
                    margin-bottom: 35px;
                }
                p {
                    position: relative;
                    height: 140px;
                    font-style: normal;
                    font-weight: 500;
                    font-size: 32px;
                    display: flex;
                    // justify-content: center;
                    align-items: center;
                }
            }
        }
    }

    .imageCards {
        height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        .imageRow:not(:last-child) {
            margin-bottom: 24px;
        }
        .imageRow {
            display: flex;
            flex-direction: row;
            .parallaxImage:not(:last-child) {
                margin-right: 24px;
            }
        }
    }

    .achievement {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 180px 0;
        h3 {
            font-style: normal;
            font-weight: 500;
            font-size: 40px;
            line-height: 140%;
            /* identical to box height, or 56px */

            display: flex;
            align-items: center;
            text-align: center;
        }
        header {
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-bottom: 150px;
            h2 {
                margin-bottom: 32px;
                font-style: normal;
                font-weight: 500;
                font-size: 120px;
                line-height: 140%;
                letter-spacing: -0.015em;
                span {
                    color: $aerovect-blue;
                }
            }
        }
        .banner {
            position: relative;
            text-align: center;
            margin-bottom: 150px;
            .globe {
                position: absolute;
                right: 0;
                top: 0;
            }
            p {
                width: 100%;
                text-align: center;
                position: absolute;
                top: 50%;
                // left: 50%;
                transform: translateY(-50%);
                font-family: Roboto;
                font-style: normal;
                font-weight: normal;
                font-size: 32px;
                line-height: 150%;
                /* or 48px */
                letter-spacing: -0.015em;
                color: white;
            }   
        }
    }
}