@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;500;700&display=swap');

html, body {
    margin: 0;
    padding: 0;
    font-family: 'Roboto';
    width: 100vw;
    overflow-x: hidden;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

@keyframes headerFadeIn {
    from { top: -120px; }
    to { top: 0; }
}

@keyframes introZoom {
    from {mask-size: 1%;}
    to {mask-size: 300%;}
}

@keyframes introExpandLine {
    from { width: 0% }
    to { width: 80% }
}

@keyframes fadeIn {
    from { opacity: 0 }
    to { opacity: 1 }
}

@keyframes introHeadingFade {
    from { bottom: -100px }
    to { bottom: 0 }
}

@keyframes introSubheadingFade {
    from { top: -50px }
    to { top: 0 }
}

@keyframes getInTouchFade {
    from { left: -50px }
    to { left: 0px }
}

// Colors;
$aerovect-blue: #0175FF;
$grey: #353535;

$page-padding-x: 400px;