@import '../../global.scss';

.headerContainer {
    height: 120px;
    width: 100vw;
    // background: red;
    position: fixed;
    top: -120px;
    z-index: 1;
    padding: 0 $page-padding-x;
    display: flex;
    justify-content: space-between;
    align-items: center;
    animation: headerFadeIn 0.5s forwards;
    animation-delay: 3.25s;

    // background: rgba(0, 0, 0, 0.5);
    z-index: 10;
    img {
        cursor: pointer;
    }
    nav {
        a:not(:last-child) {
            margin-right: 50px;
        }
        a {
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            text-decoration: none;
            // color: white;
            color: black;
        }
    }

}