@import '../../global.scss';

.careersContainer {
    header {
        padding: 0 $page-padding-x;
        height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        background: url('../../assets/Careers/backgroundLogo.svg');
        background-repeat: no-repeat;
        background-position: 100% 50%;
        h1, h2 {
            font-family: 'Roboto';
            font-style: normal;
            font-weight: 500;
            font-size: 80px;
            line-height: 140%;
            /* or 112px */

            display: flex;
            align-items: center;
            letter-spacing: -0.015em;
        }
        p {
            margin-top: 40px;
            font-style: normal;
            font-weight: 400;
            font-size: 24px;
            line-height: 150%;
            /* or 36px */

            display: flex;
            align-items: center;
            letter-spacing: -0.015em;
        }
    }
    .benefitsSection {
        height: 100vh;
        display: flex;
        // flex-direction: column;
        align-items: center;
        .groupedImg {
            position: relative;
            display: flex;
            .circleImg {
                border-radius: 50%;
                // transform: translateX(-121px);
                position: relative;
                // right: -121px;
                right: 121px;
            }
        }
        .benefits {
            display: flex;
            flex-direction: column;
            h3 {
                font-style: normal;
                font-weight: 500;
                font-size: 48px;
                margin-bottom: 50px;
            }
            .benefitsList {
                display: flex;
                justify-content: space-between;
                .benefitsCol:not(:last-child) {
                    margin-right: 21px;
                }
                .benefitsCol {
                    flex-grow: 1;
                    display: flex;
                    flex-direction: column;
                    .benefit {
                        display: flex;
                        align-items: center;
                        padding: 22px 0;
                        border-bottom: 0.5px solid #CDCDCD;
                        .imgWrapper {
                            width:24px;
                            height: 24px;
                            margin-right: 18px;
                            display: flex;
                            justify-self: center;
                            align-items: center;
                            img {
                                
                                height: 24px;
                                width: 24px;
                            }
                        }
                        p {
                            font-style: normal;
                            font-weight: 400;
                            font-size: 19px;
                        }
                    }
                }
            }
        }
    }

    .contact {
        height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        h1, h2 {
            font-family: 'Roboto';
            font-style: normal;
            font-weight: 700;
            font-size: 56px;
            line-height: 140%;
            /* or 78px */

            display: flex;
            align-items: center;
            text-align: center;
        }

        h2 {
            margin-bottom: 80px;
        }
    }
}