@import '../../global.scss';

.contactFormContainer {
    padding: 100px 40px;
    border: 0.5px solid #969696;
    display: flex;
    flex-direction: column;
    border-radius: 8px;
    input {
        width: 400px;
        margin-bottom: 48px;
        border: none;
        outline: none;
        border-bottom: 1px solid #CACACA;
        padding: 8px 0;
    }
    input::placeholder {
        color: black;
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
    }
    .submitWrapper {
        position: relative;
        button {
            position: absolute;
            right: 0;
            border-radius: 8px;
            outline: none;
            border: none;
            padding: 12px;
            background: #0175FF;
            color: white;
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
        }
    }
}

.darkMode {
    background: #353535;
    border: none;
    box-shadow: 0px 0px 16px rgba(0, 0, 0, 0.45);
    input {
        border-bottom: 1px solid #535353;
        background-color: transparent;
    }
    input::placeholder {
        color: white;
    }
}