@import '../../global.scss';

.companyContainer {
    header, .background {
        position: fixed; 
        height: 100vh;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        h1 {
            font-style: normal;
            font-weight: 500;
            font-size: 96px;
            line-height: 140%;
            color: black;
            transition: opacity 0.25s;
        }
    }

    header {
        background: white;
        z-index: 1;
    }

    .background {
        z-index: 3;
        background-color: $aerovect-blue;
        h1 {
            color: white;
        }

        mask-image: url('../../assets/Technology/circle.svg');
        mask-repeat: no-repeat;
        mask-position: 50%;
        mask-size: 150%;
    }
    .fullScreen {
        height: 100vh;
        display: flex;
        align-items: center;
    }
    .halfScreen {
        height: 50vh;
    }

    #timelineDot {
        position: fixed;
        width: 20px;
        height: 20px;
        background-color: #C4C4C4;
        border-radius: 50%;
        top: 50%;
        z-index: 3;
        left: 50%;
        transform: translateX(-50%);
        opacity: 0;
        transition: opacity 0.25s;
    }

    // #timeline::after {
    //     content: '';
    //     display: inline-block;
    //     position: absolute;
    //     top: 20px;
    //     width: 2px;
    //     left: calc((20px - 2px) / 2);
    //     // height: calc(50vh - 96px - 150px);
    //     height: 50vh;
    //     background-color: #C4C4C4;
    // }

    #timelineLine {
        position: absolute;
        top: 20px;
        width: 2px;
        left: calc((20px - 2px) / 2);
        // height: calc(50vh - 96px - 150px);
        height: 100vh;
        background-color: #C4C4C4;
    }

    .left, .right {
        display: flex;
        align-items: center;
        position: absolute;
        opacity: 1;
        transition: opacity 0.25s;
        .dot {
            width: 20px;
            height: 20px;
            position: absolute;
            background-color: #C4C4C4;
            border-radius: 50%;
            z-index: 999;
        }
        .line {
            width: 200px;
            height: 2px;
            background-image: repeating-linear-gradient(to right, #C4C4C4 0,
            #C4C4C4 1px, rgba(255,255,255,0) 1px,
            rgba(255,255,255,0) 2px);
        }

        .imageTimeline {
            display: flex;
            flex-direction: column;
            justify-content: center;
        }
        .imageDesc {
            text-align: center;
            h4 {
                margin-top: 16px;
                font-family: 'Roboto';
                font-style: normal;
                font-weight: 500;
                font-size: 16px;
                letter-spacing: -0.015em;
            }
            p {
                margin-top: 4px;
                font-style: normal;
                font-weight: 300;
                font-size: 16px;
                line-height: 140%;
                letter-spacing: -0.015em;
            }
        }
        
    }

    .left {
        right: 50%;
        .dot {
            right: -10px;
        }
        .imageDesc {
            left: 20px;
        }
    }
    .right {
        left: 50%;
        .dot {
            left: -10px;
        }
        .imageDesc {
            right: 20px;
        }
    }

    .joinUs {
        padding-bottom: 300px;
        display: flex;
        justify-content: center;
        align-items: center;
        h3 {
            font-style: normal;
            font-weight: 500;
            font-size: 40px;
        }
        a {
            text-decoration: none;
            color: $aerovect-blue;
            cursor: pointer;
        }
    }

}