@import '../../global.scss';

.footerContainer {
    height: 476px;
    width: 100vw;
    // background: red;
    // position: fixed;
    background: $grey;
    top: -120px;
    z-index: 1;
    padding: 0 $page-padding-x;
    display: flex;
    flex-direction: column;
    justify-content: center;
    .footerUpper {
        display: flex;
        width: 100%;
        justify-content: space-between;
        padding-bottom: 84px;
        border-bottom: 0.5px solid #939393;
        .logoBlock {
            img {
                margin-bottom: 24px;
            }
            p {
                color: #B4D2FF;
                font-style: normal;
                font-weight: normal;
                font-size: 16px;
                line-height: 150%;
                /* or 24px */

                letter-spacing: -0.015em;
            }
        }
        h5 {
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 150%;
            letter-spacing: -0.015em;
            color: #FFFFFF;
            margin-bottom: 18px;
        }
    }
    .addressBlock {
        address {
            font-style: normal;
            font-weight: 300;
            font-size: 16px;
            line-height: 150%;
            letter-spacing: -0.015em;
            color: #FFFFFF;
        }
    }
    .contactBlock {
        .contactRow:not(:last-child) {
            margin-bottom: 12px;
        }
        .contactRow {
            display: flex;
            align-items: center;
            img {
                margin-right: 14px;
            }
            a {
                color: white;
                text-decoration: none;
                font-style: normal;
                font-weight: 300;
                font-size: 16px;
                line-height: 150%;
                letter-spacing: -0.015em;
            }
        }
    }
    .navBlock {
        display: flex;
        flex-direction: column;
        a:not(:last-child) {
            margin-bottom: 8px;
        }
        a {
            color: white;
            text-decoration: none;
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 150%;
            letter-spacing: -0.015em;
        }
    }
    
    .footerBottom {
        margin-top: 48px;
        display: flex;
        justify-content: space-between;
        small {
            font-style: normal;
            font-weight: 300;
            font-size: 16px;
            line-height: 150%;
            letter-spacing: -0.015em;
            color: #FFFFFF;
        }
        .social {
            display: flex;
            align-items: center;
            a:not(:last-child) {
                margin-right: 40px;
            }
        }
    }
}